.header {
    height: 400px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.headerCatalog {
    background-image: url("../../../public/image/catalog_pict.jpg");
}
.headerBasket {
    background-image: url("../../../public/image/basket.jpg");
}
.headerWrapper {
    height: 76px;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.8);
    margin-bottom: 63px;
}
.headerContainer {
    max-width: 1200px;
    margin: 0 auto;
}
.headerLogo a {
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    color: white;
}
.headerHeading {
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    line-height: 70px;
    color: white;
    max-width: 792px;
}
.nav {
    display: flex;
    gap: 32px;
}
.nav a {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: white;
}

@media (max-width: 767px) {
    .header {
        padding-left: 16px;
        padding-right: 16px;
    }
    .headerWrapper {
        max-width: 767px;
    }
    .headerContainer {
        max-width: 767px;
        margin: 0 auto;
    }
    .headerLogo a {
        font-size: 36px;
    }
    .headerHeading {
        font-size: 48px;
        max-width: 767px;
        text-align: center;
    }
    .nav {
        display: flex;
        gap: 16px;
    }
    .nav a {
        font-size: 12px;
    }
}
